import React from 'react'
import { Link } from 'react-router-dom'

interface CardProps {
    title?: string,
    icon?: string;
    link?: string;
    subtitle?: string,
    children: React.ReactNode
}

export default ({ title, icon, link="/", subtitle, children }: CardProps) => {

    const header = (title && <div className="header bg-blue">
        <h2>
            {title}
{icon && <Link to={link}><i className='material-icons pull-right m-t-10'>{icon}</i></Link>}
            <small>{subtitle}</small>
        </h2>
    </div>)

    return <div className="card">
        {header}
        {children}
    </div>
}