import React, { useEffect, useState } from 'react'
import { getSellers } from '../../services/SellersService';
import { Seller } from '../../interfaces/Seller';
import CardSeller from '../../components/CardSeller/CardSeller';
import { getFavoritos } from '../../services/FavoritesService';
import TabContainer from '../../components/Tab/TabContainer';
import { Category } from '../../interfaces/Product';
import Tab from '../../components/Tab/Tab';
import Card from '../../components/Card/Card';

export default () => {
    const [categories, setCategories] = useState<Category[]>([]);
    const [sellers, setSellers] = useState<Seller[]>([]);
    const [favorites, setFavorites] = useState<string[]>([]);

    useEffect(() => {
        getSellers((result: Array<Seller>) => {
            setSellers(result)

            const sellerCategories: Category[] = [];
            result.forEach(seller => {
                if (sellerCategories.map(category => category.id).indexOf(seller.category.id) === -1) {
                    sellerCategories.push(seller.category);
                }
            })
            setCategories(sellerCategories);

        })
        getFavoritos((result: Array<string>) => setFavorites(result))
    }, [])

    return <div className='row m-l--30 m-r--30 m-t--30'>
        <TabContainer type="pills">
            {categories.map((category: Category, index: number) => <Tab active={index === 0} id={category.id} label={category.name}>
                {
                    sellers
                        .filter(seller => seller.category.id === category.id)
                        .map(seller => <div className='col-xs-12 col-md-3'>
                            <CardSeller seller={seller} favorite={favorites.indexOf(seller.id) > -1} />
                        </div>)
                }
            </Tab>)}
        </TabContainer>
    </div>
}